.home-container {
  min-height: 100vh;
  background-color: white;
}

.home-unabsorbed-status-color {
  color: #ff7875;
}

.home-undo-status-color {
  color: #1A8AFF;
}

.home-doing-status-color {
  color: #1EC644;
}

.home-done-status-color {
  color: rgba(0, 0, 0, 1);
}

.home-cancel-status-color {
  color: #BCBCBC;
}

@home-content: 4vw;

.home-content-scroll {
  position: fixed;
  overflow: hidden;
  left: 0;
  right: 0;
  padding: 0 @home-content;
  top: 50vw;
  bottom: 19vw;
  // width: 100%;
  overflow: auto;
}

.home-content-no-foot {
  bottom: 0;
}

.home-content {
  width: 100%;
  padding-top: 6vw;
  overflow: auto;
}

.home-content-foot {
  padding: 3vw;
  text-align: center;
  color: gray;
}

.home-content-head {
  position: fixed;
  z-index: 10;
  top: 34vw;
  left: @home-content;
  right: @home-content;
  display: flex;
  height: 20vw;
  justify-content: center;
  align-items: center;
  text-align: center;
  border-radius: 3vw;
  box-shadow: 0 0 1vw #ccc;
  background: white;
  padding: 2vw;

  .home-content-head-item {
    position: relative;
    width: 20%;
  }

  .home-content-head-item-dot {
    width: 2vw;
    position: absolute;
    right: 0vw;
    top: 0.5vw;
  }

  .home-content-head-item-num {
    font-weight: bold;
    margin-bottom: 1vw;
    font-size: 4.5vw;
  }

  .home-content-head-item-status {
    font-size: 3.5vw;
    color: gray;
  }

  img {
    width: 5vw;
    margin-right: 2vw;
  }
}

.home-content-item {
  display: flex;
  align-items: center;
  padding: 3vw 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);

  .home-content-item-icon {
    width: 7vw;
    margin-right: 2vw;
  }

  .home-content-item-name {
    font-size: 5vw;
    font-weight: 500;

  }

  .home-content-item-tag {
    display: inline-block;
    font-size: 3vw;
    border-radius: 1vw;
    padding: 0.2vw 1vw;
    border: 1px solid gray;
    color: gray;
    margin-left: 3vw;
    vertical-align: text-top;
  }

  .home-content-item-right {
    flex: 1;
    text-align: right;
    font-size: 5vw;

    img {
      width: 6vw;
    }
  }
}

.home-content-passenger-item {
  padding: 3vw 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);

  .home-content-passenger-item-head,
  .home-content-passenger-item-child {
    margin-left: -1.3vw;
  }

  .home-content-passenger-item-head {
    display: flex;
    padding: 1.5vw 0;
    align-items: center;

    .home-content-passenger-item-head-city,
    .home-content-passenger-item-head-plane {
      font-size: 4.2vw;
      font-weight: 500;
    }

    .home-content-passenger-item-head-icon-city,
    .home-content-passenger-item-head-icon-plane {
      width: 6vw;
    }

    .home-content-passenger-item-head-icon-plane {
      margin-right: 2vw;
    }

    .home-content-passenger-item-head-type {
      span {
        display: flex;
        font-size: 2.3vw;
        height: 4.8vw;
        align-items: center;
        border-radius: 0.5vw;
        padding: 0vw 0.8vw;
        border: 1px solid #1A8AFF;
        color: #1A8AFF;
      }
      
      margin: 0vw 4vw 0 2vw;
    }

    .home-content-passenger-item-head-status {
      flex: 1;
      text-align: right;
      font-size: 3.8vw;
    }

  }

  .home-content-passenger-item-child {
    display: flex;
    align-items: center;
    color: black;
    font-size: 3.2vw;
    padding: 0.5vw 0;

    img {
      width: 6vw;
    }
  }

  .home-content-passenger-item-child-last {
    color: rgba(0, 0, 0, 0.6);
    font-size: 3.5vw;
  }

  .home-content-passenger-item-childline {
    position: relative;

    span {
      position: absolute;
      left: 1.45vw;
      font-size: 2vw;
      font-weight: bold;
      transform: translate(0, -60%);
      color: rgba(0, 0, 0, 0.2);
    }


  }
}
