body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

}

body * {
  font-size: 3.7vw;
  line-height: 1.5;
  box-sizing: border-box;
  font-family: PingFangSC-Regular, PingFang SC;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.route-container {
  min-height: 100vh;
  background: white;

  .am-button {
    height: auto;
    line-height: 1.5;
    padding: 2.5vw;
    font-size: 4vw;
  }

  .am-list .am-list-header {
    padding: 0;
    color: gray;
  }

  .am-list-body::before {
    opacity: 0;
  }

  .am-list-body .am-list-item.am-input-item {
    padding-left: 0;
  }

  .am-list-item {
    padding-left: 0;
  }

  .am-list-content {
    display: none;
  }

  .am-list-item .am-list-line .am-list-extra {
    flex-basis: 100%;
    text-align: left;
  }

  .am-image-picker-list {
    padding: 1vw 0 0;
  }

  .am-image-picker-list .am-flexbox .am-flexbox-item {
    margin-right: 0;
  }

  .am-image-picker-list .am-flexbox .am-flexbox-item:after {
    padding-bottom: 38vw;
  }

}

body .am-modal {
  .am-modal-header {
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    margin-bottom: 5vw;
  }

  .in-am-modal-content {
    padding: 0 6vw;
  }

  .in-am-modal-content-right,
  .in-am-modal-content-left {
    display: inline-block;
    vertical-align: middle;
    text-align: left;
  }

  .in-am-modal-content-left {
    width: 40%;
    font-size: 3.1vw;
    color: rgba(0, 0, 0, 0.8);
    padding: 2vw 0;
  }

  .in-am-modal-content-right {
    width: 60%;
    font-size: 4vw;
  }

}

.background-map {
  background-image: url(../imgs/img_map.png);
  background-size: 100%;
  background-repeat: no-repeat;
}

.background-home {
  background-image: url(../imgs/img_home_bg.png);
  background-size: 100%;
  background-repeat: no-repeat;
}

.driver-page-info {
  margin-top: 25vw;
  height: calc(100vh - 43vw);
  overflow: auto;

  .driver-page-info-head {
    position: fixed;
    top: 8vw;
    left: 8vw;
    right: 8vw;
    text-align: center;
    font-size: 4vw;

    img {
      width: 30vw;
    }
  }
}

.driver-page-review {
  margin-top: 18vw;
}

.driver-page-detail {
  margin-top: 16vw;
  height: calc(100vh - 33vw);
  overflow: auto;
  border-radius: 3vw;

}


.detail-page-home {
  top: 15vw;
  bottom: 0;
}

.login-page-head {
  text-align: center;
  margin-top: 7vw;
  margin-bottom: 3vw;

  .login-page-head-icon {
    width: 18vw;
  }

  .login-page-head-title {
    font-size: 4vw;
    margin-bottom: 3vw;
    color: black;
  }

  .login-page-head-bg {
    width: 66vw;
  }
}

.login-page-button {
  margin-top: 8vw;
}
