.detail-container {
  background-color: #F2F2F2;
  min-height: 100vh;
  padding: 15vw 2vw 0vw;




}

.detail-content-scroll {
  border-top-left-radius: 3vw;
  border-top-right-radius: 3vw;
  padding-top: 15vw;
  padding-bottom: 2vw;
  height: calc(100vh - 15vw);
  overflow: auto;
}

.detail-content {
  border-radius: 3vw;
  background: white;
  overflow: auto;
  padding: 4vw;
}

.detail-content-container {
  padding: 3vw 0 6vw;

}


.detail-content-head {
  text-align: center;
  font-weight: 600;
  font-size: 4vw;

  img {
    width: 35vw;
  }

  /* font-size: 4vw; */
}

.detail-content-subhead {
  font-size: 5vw;
  font-weight: 500;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  padding: 2vw 0;
  margin-bottom: 3vw;
}

.detail-content-line-item {
  margin: 2vw 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.detail-content-item {

  /* padding: 5vw 0; */


  .detail-content-item-left,
  .detail-content-item-right {
    display: inline-block;
    vertical-align: middle;
  }

  .detail-content-item-top {
    vertical-align: top;
    padding: 2.5vw 0;
  }

  .detail-content-item-left {
    width: 25%;
    color: rgba(0, 0, 0, 0.8);
    padding: 2.5vw 0;
    font-size: 3.3vw;


  }


  .detail-content-item-right {
    width: 75%;
    font-size: 4.3vw;
    padding: 2vw 0;

    .am-list-body::after {
      border-bottom: 1px solid rgba(0, 0, 0, 0.1);
    }

    .am-list-item .am-input-control input {
      font-size: 4.4vw;
    }

    img {
      // width: 30vw;
      width: 100%;
      height: 38vw;
      border-radius: 2vw;
      object-fit: cover;
    }
  }

  .detail-content-item-border {
    border-bottom: 1px solid rgba(0, 0, 0, 0.1);
  }

}

.detail-content-button {
  margin-top: 8vw;
}
